* {
  box-sizing: border-box;
}

html, body, #root {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
}

.html-content-container {
  z-index: 1;
  flex-direction: column;
  line-height: 26px;
  color: rgb(136, 146, 176);
  width: 100%;
}

.logo {
  width: 42px;
  height: 42px;
  /* Generated with: https://codepen.io/sosuke/pen/Pjoqqp */
  filter: invert(55%) sepia(69%) saturate(353%) hue-rotate(128deg) brightness(100%) contrast(100%);
}

.header {
  align-items: center;
  justify-content: space-between;
  backdrop-filter: blur(10px);
  /* background-color: rgba(23, 36, 42, .7); */
  background-color: rgba(10, 25, 47, .85);
  top: 0px;
  z-index: 12;
  box-shadow: rgba(2, 12, 27, 0.7) 0px 10px 30px -10px;
  padding-left: 50px;
  padding-right: 50px;
  padding-top: 0px;
  padding-bottom: 0px;
}

.navbar {
  height: 70px;
  align-items: center;
  display: flex;
  font-size: 20px;
  justify-content: space-between;
  position: relative;
  z-index: 13;
}

.nav-menu {
  display: flex;
  align-items: center;
  box-sizing: border-box;
}

/* Hide the nav bar on mobile until we can make it collapsible */

@media (max-width:650px) {
  .navbar {
    display: none !important;
  }
}

.menu-ordered-list {
  display: flex;
  align-items: center;
}

.menu-ordered-list>li {
  box-sizing: border-box;
  font-size: 14px;
  font-family: "SF Mono", "Fira Code", "Fira Mono", "Roboto Mono", monospace;
  color: rgb(204, 214, 246);
  margin-left: 10px;
  margin-right: 20px;
  display: inline;
  line-height: 16.9px;
}

ol {
  counter-reset: item;
}

ol li {
  display: block;
}

ol li:before {
  content: "0" counter(item) ". ";
  counter-increment: item;
  color: #3AAFA9;
}

.content-container {
  padding-left: 0px;
  padding-right: 0px;
}

/*  */

/* Video Sandbox */

/*  */

.cover-screen {
  z-index: -10;
  position: fixed;
  width: 100%;
  height: auto;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  overflow: hidden;
}

@media (max-aspect-ratio: 16/9) {
  .cover-screen {
    width: auto;
    min-height: 100%;
  }
}

@media (max-aspect-ratio: 16/9) {
  .cover-screen {
    height: 100%;
  }
}

/*  */

/* Sections */

/*  */

section {
  height: 95vh;
  padding: 0px;
  margin: 0px;
}

.no-bg {
  background-color: rgba(0, 0, 0, 0);
}

.no-bg>* {
  background-color: rgba(0, 0, 0, 0);
}

.section-1>p {
  background-color: rgba(0, 0, 0, 0);
}

.section-2 {
  background-color: #17242A;
}

.section-3 {
  background-color: rgba(0, 0, 0, 0);
}

.section-4 {
  background-color: #3AAFA9;
}

.section-5 {
  background-color: rgba(0, 0, 0, 0);
}

.section-6 {
  background-color: #17242A;
}

/*  */

/* Section 1 Text */

/*  */
.centered-content {
  /* background: rgba(0, 0, 0, 0); */
  /* Center the text container */
  width: 40%;
  text-align: center;
  /* Center text */
  position: absolute;
  /* Position text */
  top: 50vh;
  /* Position text in the middle */
  left: 50vw;
  /* Position text in the middle */
  transform: translate(-50%, -50%);
  /* Position text in the middle */
}

.section-1 h2 {
  background-color: rgba(4, 9, 12, 0.2);
  color: white;
  font-size: 5vw;
  text-transform: uppercase;
  /* Responsive font size */
  font-weight: bold;
  margin: 0 auto;
  /* Center the text container */
  padding-bottom: 0px;
  width: 100%;
  text-align: center;
  /* This makes the cutout text possible */
  line-height: 8vw;
  backdrop-filter: blur(30px);

}

.section-1 h3 {
  background-color: rgba(58, 175, 169, .5);
  color: white;
  font-size: 2vw;
  text-transform: uppercase;
  font-weight: normal;
  margin: 0 auto;
  margin-right: 1vw;
  margin-top: -1vw;
  /* Center the text container */
  padding: 10px;
  width: 65%;
  text-align: center;
  /* This makes the cutout text possible */
  line-height: 2.2vw;
  backdrop-filter: blur(100px);
}

@media (max-width:650px) {
  .section-1 h3 {
    line-height: .2vw;
  }
}


.color-reference {
  color: #17242A;
  color: #2B7A77;
  color: #3AAFA9;
  color: #DEF2F1;
  color: #FFFFFF;
}

@media (max-width:650px) {
  .media-query-reference {
    display: none !important;
  }
}

